import { Box, Typography } from '@material-ui/core';
import React, { memo } from 'react';
import { useInputsFormStyles } from '../styles';
import cn from 'classnames'

export const FormError = memo(({ message, className = '', style }) => {

    const { form__error } = useInputsFormStyles()

    return (
        <Box className={cn(form__error, { [className]: className && true })}
            style={style}>
            <Typography variant='body1'>
                {message}
            </Typography>
        </Box>
    );
});