import React, { Profiler } from 'react';
import DocumentTitle from 'react-document-title';
import { profilerOnRender } from '../common/profiler/profilerOnRender';
import { LoginForm } from './loginReduxForm/LoginReduxForm';

export const Login = ({ login, captchaUrl }) => {
    return (
        <>
            <DocumentTitle title='Login' />
            <Profiler id='login-profiler' onRender={profilerOnRender}>
                <LoginForm captchaUrl={captchaUrl} onSubmit={login} />
            </Profiler>
        </>
    )
};