import React, { memo } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { getAuthData } from '../../redux/selectors/auth-selectors';
import { logOffServiceT } from '../../redux/reducers/auth-reducer'
import { Header } from './Header';

const Wrapper = (props) => {
    return <Header {...props} />
};

export const HeaderContainer = compose(
    connect((state) => ({
        authData: getAuthData(state)
    }), {
        logOffServiceT
    }),
    memo
)(Wrapper);