import { Box, Button, Paper } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { compose } from 'redux';
import { reduxForm } from 'redux-form';
import { booleanCheckCondition } from '../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { FormError } from '../../common/inputs/formError/FormError';
import { FormInputField } from '../../common/inputs/formInputField/FormInputField';
import { InputCheckbox } from '../../common/inputs/inputCheckbox/InputCheckbox';
import { InputStandard } from '../../common/inputs/inputStandard/InputStandard';
import { required } from '../../common/inputs/validate/ValidateInput';
import { useLoginStyles } from '../styles';

const Login = memo(({ handleSubmit, captchaUrl, error, pristine, submitting }) => {

    const {
        form__login,
        form__login__$wrapper,
        form__login__field,
        form__login__btn_$wrapper,
        form__login__btn_$submit,
        captcha_$wrapper,
        captcha_$img
    } = useLoginStyles()

    const isDisabledSubmitBtn = pristine || submitting

    return (
        <Box className={form__login}>
            <form id='Login' onSubmit={handleSubmit} style={{ width: '100%' }}>
                <Paper className={form__login__$wrapper}>
                    <FormInputField
                        name='email'
                        component={InputStandard}
                        validations={[required]}
                        castomize={{
                            type: 'email',
                            label: 'Email',
                            variant: 'outlined',
                            className: form__login__field,
                            'aria-label': 'Login email',
                            'aria-required': true,
                            inputMode: 'email'
                        }} />

                    <FormInputField
                        name='password'
                        component={InputStandard}
                        validations={[required]}
                        castomize={{
                            type: 'password',
                            label: 'Password',
                            variant: 'outlined',
                            className: form__login__field,
                            'aria-label': 'Login assword',
                            'aria-required': true,
                            inputMode: 'text'
                        }} />

                    <FormInputField
                        name='rememberMe'
                        component={InputCheckbox}
                        validations={[]}
                        castomize={{
                            type: 'checkbox',
                            label: 'Remember me',
                            variant: '',
                            className: form__login__field,
                            'aria-label': 'Login remember me'
                        }} />

                    {booleanCheckCondition(
                        captchaUrl,
                        <Box className={captcha_$wrapper}>
                            <Box className={captcha_$img}>
                                <img alt="captcha" src={captchaUrl} />
                            </Box>
                            <FormInputField
                                name='captcha'
                                component={InputStandard}
                                validations={[required]}
                                castomize={{
                                    type: 'text',
                                    size: 'small',
                                    'aria-label': 'Login Your text from captcha',
                                    'aria-required': true,
                                    style: {
                                        width: '100%'
                                    },
                                }} />
                        </Box>
                    )}

                    {booleanCheckCondition(
                        error,
                        <FormError message={error} style={{ marginBottom: '20px' }} />
                    )}

                    <Box className={form__login__btn_$wrapper}>
                        <Button
                            disabled={isDisabledSubmitBtn}
                            variant='outlined'
                            type='submit'
                            className={form__login__btn_$submit}>
                            Login
                        </Button>
                    </Box>
                </Paper>
            </form>
        </Box>
    );
});

export const LoginForm = compose(
    reduxForm({ form: 'login' }),
    memo
)(Login);