import { Button } from '@material-ui/core';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { useNavbarStyles } from '../../../styles';

export const NavbarItem = ({ urlLink, ariaLabelBtn, children }) => {

    const {
        navbar__link,
        link_$wrapper,
        link,
        link_$selected
    } = useNavbarStyles()

    return (
        <li className={navbar__link}>
            <Button aria-label={ariaLabelBtn} className={link_$wrapper}>
                <NavLink className={link} activeClassName={link_$selected} to={urlLink}>
                    {children}
                </NavLink>
            </Button>
        </li>
    );
};