import { Box } from '@material-ui/core';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { useAppHeaderStyles } from '../styles';

export const MainLogoApp = () => {

    const {
        header__logo_$wrapper,
        logo__link,
        link__img
    } = useAppHeaderStyles()

    return (
        <Box className={header__logo_$wrapper}>
            <NavLink className={logo__link} to={'/feed'}>
                <img className={link__img} alt='mainAppLogo' src={'https://image.flaticon.com/icons/svg/733/733547.svg'} />
            </NavLink>
        </Box>
    );
};