export const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {

        case SEND_MY_MESSAGE:
            return setStateValueInObject(
                state,
                'messages',
                setStateValueInObject(
                    state.messages,
                    'messagesData',
                    addValueInArray(
                        state.messages.messagesData,
                        { id: 4, ...action.payload }
                    )
                )
            )

        default: return {
            ...state
        }
    }
}

const sendDialogMessage = (message, userId, userMessageLogo, fullName) => ({
    type: SEND_MY_MESSAGE,
    payload: {
        message,
        userId,
        userMessageLogo,
        fullName
    }
})


export const sendDialogMessageT = ({
    formData: { addMessage },
    userId,
    photos: { small },
    loginName
}) => (dispatch) => {
    dispatch(sendDialogMessage(addMessage, userId, small, loginName))
    dispatch(reset('dialogs-message'))
}

