import React from 'react';
import { Redirect } from 'react-router-dom';
import { booleanCheckCondition } from '../../../../utils/utilsForHandlingCondition/booleanCheckCondition';

export const withRedirect = (condition, Component, props, link) => {
    return booleanCheckCondition(
        condition,
        <Redirect to={`/${link}`} />,
        <Component {...props} />
    )
}