import { stopSubmit } from 'redux-form';
import { authApi, profileApi } from '../../api/Api';
import { booleanCheckCondition } from '../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { expendObjPropsInObject } from '../../utils/utilsForReducers/expendObjPropsInObject';
import { setStateValueInObject } from '../../utils/utilsForReducers/setStateValueInObject';

const SET_USER_DATA = 'social-network/auth-reducer/SET_AUTH_DATA',
    SET_CAPTCHA_URL = 'social-network/profile-reducer/SET_CAPTCHA_URL';

const initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    isFetching: false,
    captchaUrl: null,
}

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return expendObjPropsInObject(state, action.payload)

        case SET_CAPTCHA_URL:
            return setStateValueInObject(
                state,
                'captchaUrl',
                action.url
            )

        default: return {
            ...state
        }
    }
}

const setUserDataAC = (data) => ({
    type: SET_USER_DATA,
    payload: { ...data }
}),

    sendCaptchUrl = (url) => ({
        type: SET_CAPTCHA_URL,
        url
    });


export const getAuthDataT = () => async (dispatch) => {
    const { id, email, login, resultCode } = await authApi.getAuthData()

    if (resultCode === 0) {
        const { data: { photos } } = await profileApi.getProfileUserInfo(id)
        dispatch(setUserDataAC({
            id,
            email,
            login,
            photos,
            isAuth: true,
            captchaUrl: null
        }))
    }
}

export const getCaptchUrlT = () => async (dispatch) => {
    const { data: { url } } = await authApi.getCaptchUrl()
    dispatch(sendCaptchUrl(url))
}

export const logOffServiceT = () => async (dispatch) => {
    const { resultCode } = await authApi.logOffService()

    booleanCheckCondition(
        resultCode === 0,
        dispatch(setUserDataAC({
            id: null,
            email: null,
            login: null,
            photos: null,
            isAuth: false,
            captchaUrl: null
        }))
    )
}


export const loginT = ({
    email,
    password,
    rememberMe = false,
    captcha = null
}) => async (dispatch) => {

    const { resultCode, messages = false } = await authApi.logInService(email, password, rememberMe, captcha)

    if (resultCode === 0) {
        dispatch(getAuthDataT())
    }

    if (resultCode === 1) {
        dispatch(stopSubmit('login', {
            _error: messages[0]
        }))
    }

    if (resultCode === 10) {
        dispatch(getCaptchUrlT())
    }
}

