import { Box, Container, Typography } from '@material-ui/core';
import React, { lazy, memo, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import './App.css';
import { CircularPreloader } from './components/common/preloader/CircularPreloader';
import { HeaderContainer } from './components/header/HeaderContainer';
import { LoginContainer } from './components/login/LoginContainer';
import { MusicContainer } from './components/music/MusicContainer';
import { NavbarContainer } from './components/navbar/NavbarContainer';
import { NewsContainer } from './components/news/NewsContainer';
import { SettingsContainer } from './components/settings/SettingsContainer';

const ProfileContainer = lazy(() => import('./components/profile/ProfileContainer'));
const DialogsContainer = lazy(() => import('./components/dialogs/DialogsContainer'));
const UsersContainer = lazy(() => import('./components/users/UsersContainer'));

export const App = memo(() => {
	return (
		<Box className='app'>
			<HeaderContainer />
			<section>
				<Container maxWidth='lg'>
					<Box className='app__content'>
						<NavbarContainer />
						<main className='app__content-main'>
							<Suspense fallback={<CircularPreloader />}>
								<Switch>
									<Route path={'/profile/:userId?'} render={() => <ProfileContainer />} />
									<Route path={'/dialogs'} render={() => <DialogsContainer />} />
									<Route path={'/users'} render={() => <UsersContainer />} />
									<Route path={'/login'} render={() => <LoginContainer />} />
									<Route path={'/feed'} render={() => <NewsContainer />} />
									<Route path={'/settings'} render={() => <SettingsContainer />} />
									<Route path={'/music'} render={() => <MusicContainer />} />
									<Route path={'*'} render={() => <Typography>404</Typography>} />
									<Redirect from={'/'} to={'/profile'} />
								</Switch>
							</Suspense>
						</main>
					</Box>
				</Container>
			</section>
		</Box>
	);
})
