import { reset, stopSubmit } from 'redux-form';
import { profileApi } from '../../api/Api';
import { addValueInArray } from '../../utils/utilsForReducers/addValueInArray';
import { setStateValueInObject } from '../../utils/utilsForReducers/setStateValueInObject';
import { removeObjectInArray } from '../../utils/utilsForReducers/removeObjectInArray';

const SET_PROFILE_DATA = 'social-network/profile-reducer/SET_PROFILE_DATA',
    SET_PROFILE_STATUS = 'social-network/profile-reducer/SET_PROFILE_STATUS',
    SAVE_PROFILE_PHOTO = 'social-network/profile-reducer/SAVE_PROFILE_PHOTO',
    GET_PROFILE_USER_POSTS = 'social-network/profile-reducer/SAVE_PROFILE_USER_POSTS',
    ADD_PROFILE_POST = 'social-network/profile-reducer/ADD_PROFILE_POST',
    REMOVE_PROFILE_POST = 'social-network/profile-reducer/REMOVE_PROFILE_POST';

const initialState = {
    profile: null,
    status: '',
    posts: [],
    isFetching: false
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_PROFILE_DATA:
            return setStateValueInObject(state, 'profile', action.payload)

        case SET_PROFILE_STATUS:
            return setStateValueInObject(state, 'status', action.status)

        case GET_PROFILE_USER_POSTS:
            return setStateValueInObject(state, 'posts', action.payload)

        case ADD_PROFILE_POST:
            return setStateValueInObject(
                state,
                'posts',
                addValueInArray(
                    state.posts,
                    { userId: 1, id: 11, ...action.payload }
                )
            )

        case REMOVE_PROFILE_POST:
            return setStateValueInObject(
                state,
                'posts',
                removeObjectInArray(state.posts, 'id', action.postId)
            )

        case SAVE_PROFILE_PHOTO:
            return setStateValueInObject(
                state,
                'profile',
                setStateValueInObject(state.profile, 'photos', action.payload)
            )

        default: return {
            ...state
        }
    }
}

export const setProfileData = (data) => ({
    type: SET_PROFILE_DATA,
    payload: { ...data }
}),

    setProfileStatus = (status) => ({
        type: SET_PROFILE_STATUS,
        status
    }),

    setProfilePhoto = (photos) => ({
        type: SAVE_PROFILE_PHOTO,
        payload: { ...photos }
    }),

    setProfileUserPosts = (posts) => ({
        type: GET_PROFILE_USER_POSTS,
        payload: [...posts]
    }),

    addProfilePost = (data) => ({
        type: ADD_PROFILE_POST,
        payload: { ...data }
    }),

    removeProfilePost = (postId) => ({
        type: REMOVE_PROFILE_POST,
        postId
    })



export const setProfileDataT = (id) => async (dispatch) => {
    const { data } = await profileApi.getProfileUserInfo(id)
    dispatch(setProfileData(data))
};

export const getProfileStatusT = (id) => async (dispatch) => {
    const { data } = await profileApi.getProfileStatus(id)
    dispatch(setProfileStatus(data))
}

export const updateProfileStatusT = (status, id) => async (dispatch) => {
    const { data: { resultCode } } = await profileApi.updateProfileStatus(status)
    if (resultCode === 0) {
        dispatch(getProfileStatusT(id))
    }
}

export const saveProfilePhotoT = (file) => async (dispatch) => {
    const { data: { photos }, resultCode } = await profileApi.saveProfilePhoto(file);
    if (resultCode === 0) {
        dispatch(setProfilePhoto(photos))
    }
}

export const setProfileSettingsT = (profileData, userId) => async (dispatch) => {
    const { data: { messages, resultCode } } = await profileApi.setProfileSettings(profileData)
    if (resultCode === 0) {
        dispatch(setProfileDataT(userId))
    } else {
        let message = messages.length
            ? messages
            : 'Some error'

        dispatch(stopSubmit('edit-profile-settings', {
            _error: message
        }))

        return Promise.reject(message)
    }
}

export const getProfileUserPostsT = (userId) => async (dispatch) => {
    const { data } = await profileApi.getProfileUserPosts(userId);
    dispatch(setProfileUserPosts(data))
}

export const addProfilePostT = (data) => (dispatch) => {
    dispatch(addProfilePost(data))
    dispatch(reset('add-post'))
}

export const removeProfilePostT = (id) => (dispatch) => {
    dispatch(removeProfilePost(id))
}

