import React, { memo } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { getCaptchUrl } from '../../redux/selectors/auth-selectors';
import { loginT } from '../../redux/reducers/auth-reducer';
import { Login } from './Login';
import { SubmissionError } from 'redux-form';
import { withAuthRedirect } from '../common/hoc/withAuthRedirect/withAuthRedirect';

const Wrapper = ({ loginT, ...props }) => {

    const login = (formData) => {
        loginT(formData).catch(error => {
            if (error.validationErrors) {
                throw new SubmissionError(error.validationErrors)
            }
        })
    }

    return <Login {...props} login={login} />
}

export const LoginContainer = compose(
    connect((state) => ({
        captchaUrl: getCaptchUrl(state)
    }), {
        loginT
    }),
    memo,
    withAuthRedirect
)(Wrapper, 'feed', 'default');