import { makeStyles } from '@material-ui/core';

export const useNavbarStyles = makeStyles((theme) => ({
    app__navbar: {
        padding: '20px',
        display: 'flex',
        justifyContent: 'stretch',
        alignItems: 'stretch',
        maxWidth: '100%',
        position: 'sticky',
        boxSizing: 'border-box',
        top: '64px',
        [theme.breakpoints.down('sm')]: {
            width: '100%',
            top: 'auto',
            bottom: '0',
            left: '0',
            right: '0',
            position: 'fixed',
            zIndex: '1000',
            backgroundColor: '#fff',
            padding: '15px'
        },
        [theme.breakpoints.down('xs')]: {
            padding: '10px'
        }
    },
    navbar__content: {
        width: '100%',
        height: '100%',
    },

    content_$wrapper: {
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        alignItems: 'flex-start',
        [theme.breakpoints.down('sm')]: {
            flexDirection: 'row',
            alignItems: 'center',
        }
    },

    navbar__link: {
        minWidth: '100%',
        marginBottom: '20px',
        '&:last-child': {
            marginBottom: '0'
        },
        [theme.breakpoints.down('sm')]: {
            minWidth: '0',
            width: '100%',
            marginBottom: '0'
        }
    },

    link_$wrapper: {
        width: '100%',
        padding: '0',
        minWidth: '32px'
    },

    link: {
        width: '100%',
        height: '100%',
        color: '#333333',
        display: 'flex',
        flexDirection: 'row',
        padding: '8px',
        justifyContent: 'flex-start',
        alignItems: 'center',
        borderRadius: '4px',
        touchAction: 'manipulation',
        [theme.breakpoints.down('sm')]: {
            justifyContent: 'center'
        }
    },

    link_$selected: {
        backgroundColor: 'rgb(51 51 51 / 8%)'
    },

    link_$text: {
        color: '#333333',
        display: 'flex',
        width: '100%',
        flexDirection: 'row',
        marginLeft: '10px',
        height: '100%',
        justifyContent: 'flex-start',
        alignItems: 'center',
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        }
    }
}));