import Axios from 'axios';

const INSTANCE_BASE = Axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        'API-KEY': '66ba8164-5cb6-49a4-a723-0c6dbe7ae345',
    }
})

const INSTANCE_JSON_PLACHOLDER = Axios.create({
    baseURL: 'https://jsonplaceholder.typicode.com/'
})

export const authApi = {
    async getAuthData() {
        const {
            data: {
                data: {
                    id,
                    email,
                    login
                },
                resultCode
            }
        } = await INSTANCE_BASE.get('auth/me')
        return { id, email, login, resultCode }
    },

    async logOffService() {
        const { data: { resultCode } } = await INSTANCE_BASE.delete(`auth/login`);
        return { resultCode }
    },

    async logInService(email, password, rememberMe, captcha) {
        const { data: { resultCode, messages } } = await INSTANCE_BASE.post(`auth/login`, {
            email,
            password,
            rememberMe,
            captcha
        });
        return { resultCode, messages }
    },

    async getCaptchUrl() {
        const { data } = await INSTANCE_BASE.get(`security/get-captcha-url`)
        return { data }
    }
};

export const profileApi = {
    async getProfileUserInfo(id) {
        const { data } = await INSTANCE_BASE.get(`profile/${id}`)
        return { data }
    },

    async getProfileStatus(id) {
        const { data } = await INSTANCE_BASE.get(`profile/status/${id}`);
        return { data }
    },

    async saveProfilePhoto(file) {
        const formData = new FormData();
        formData.append('image', file);
        const { data: { resultCode, data } } = await INSTANCE_BASE.put(`profile/photo`, formData);
        return { resultCode, data }
    },

    async setProfileSettings(profileData) {
        const { data } = await INSTANCE_BASE.put(`profile`, profileData)
        return { data }
    },

    async updateProfileStatus(status) {
        const { data } = await INSTANCE_BASE.put(`profile/status`, { status: status })
        return { data }
    },

    async getProfileUserPosts() {
        const { data } = await INSTANCE_JSON_PLACHOLDER.get(`posts?userId=1`)
        return { data }
    }
}

export const usersApi = {
    async getAllUsersOnActivePage(count, page) {
        const { data } = await INSTANCE_BASE.get(`users?page=${page}&count=${count}`)
        return { data }
    },

    async setUserFollow(id) {
        const { data: { resultCode } } = await INSTANCE_BASE.post(`follow/${id}`)
        return { resultCode }
    },

    async setUserUnFollow(id) {
        const { data: { resultCode } } = await INSTANCE_BASE.delete(`follow/${id}`)
        return { resultCode }
    }
}
