import { getAuthDataT } from './auth-reducer';
import { setStateValueInObject } from '../../utils/utilsForReducers/setStateValueInObject'

const INITIALIZATION_APP = 'social-network/app-reducer/INITIALIZATION_APP';

const initialState = {
    initialization: false
}

export const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZATION_APP:
            return setStateValueInObject(state, 'initialization', true)

        default: return {
            ...state
        }
    }
}

const setInitializationAC = () => ({
    type: INITIALIZATION_APP
})


export const setInitializationT = () => async (dispatch) => {
    let authMePromise = await dispatch(getAuthDataT())

    await Promise.allSettled([authMePromise])
    dispatch(setInitializationAC())
}

