import { makeStyles } from '@material-ui/core';

export const useAppHeaderStyles = makeStyles((theme) => ({
    app__header: {
        backgroundColor: '#2240c1',
        boxShadow: `0px 2px 1px -1px rgba(0,0,0,0.2),
        0px 1px 1px 0px rgba(0,0,0,0.14),
        0px 1px 3px 0px rgba(0,0,0,0.12)`
    },

    header__root: {
        flexGrow: 1
    },

    header__toolbar: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'stretch'
    },

    header__logo_$wrapper: {
        display: 'grid',
        placeItems: 'center',
        width: '64px'
    },

    logo__link: {
        height: '100%',
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },

    link__img: {
        display: 'block',
        height: '30px'
    },

    header__nav: {
        display: 'grid',
        placeItems: 'center'
    },

    nav__content_$wrapper: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center'
    },

    nav__profile_$icon: {
        color: '#ffffff'
    },

    nav__logoff_$icon: {
        color: '#ffffff'
    },

    nav__login: {
        color: '#ffffff'
    }
}));