export const getAuthData = ({ auth }) => {
    return auth
},

    getIsAuth = ({ auth: { isAuth } }) => {
        return isAuth
    },

    getUserId = ({ auth: { id } }) => {
        return id
    },

    getLoginName = ({ auth: { login } }) => {
        return login
    },

    getIsFetching = ({ auth: { isFetching } }) => {
        return isFetching
    },

    getEmail = ({ auth: { email } }) => {
        return email
    },

    getUserLogoPhotos = ({ auth: { photos } }) => {
        return photos
    },

    getCaptchUrl = ({ auth: { captchaUrl } }) => {
        return captchaUrl
    }