import React, { useEffect } from 'react';
import { memo } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { App } from './App';
import { AppInitialization } from './AppInitialization';
import { setInitializationT } from './redux/reducers/app-reducer';
import { getInitialization } from './redux/selectors/app-selectors';
import { booleanCheckCondition } from './utils/utilsForHandlingCondition/booleanCheckCondition';


const AppContentContainer = ({ setInitializationT, initialization }) => {

    useEffect(() => {
        setInitializationT()
    }, [setInitializationT])

    return booleanCheckCondition(
        initialization,
        <App />,
        <AppInitialization />
    )
};

export const AppContainer = compose(
    connect((state) => ({
        initialization: getInitialization(state)
    }), {
        setInitializationT
    }),
    memo
)(AppContentContainer);