import { usersApi } from '../../api/Api';
import { setStateValueInObject } from '../../utils/utilsForReducers/setStateValueInObject';
import { addValueInArray } from '../../utils/utilsForReducers/addValueInArray'
import { booleanCheckCondition } from '../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { loopThroughArrayWithCallback } from '../../utils/arrayFunctions/loopThroughArrayWithCallback';

const SET_ALL_USERS_ON_ACTIVE_PAGE = 'social-network/users/SET_ALL_USERS_ON_ACTIVE_PAGE',
    SET_ALL_USERS_IN_SERVICE = 'social-network/users/SET_ALL_USERS_IN_SERVICE',
    SET_IS_FETCHING_USERS = 'social-network/profile-reducer/SET_IS_FETCHING_USERS',
    SET_SUBSCRIBE_TO_USER = 'social-network/profile-reducer/SET_SUBSCRIBE_TO_USER',
    SET_IS_FETCHING_FOLLOW_BUTTON = 'social-network/profile-reducer/SET_IS_FETCHING_FOLLOW_BUTTON';

const initialState = {
    allUsersOnActivePage: [],
    allUsersInService: 0,
    pageSize: 50,
    activePage: 1,
    isFetchingUsers: null,
    isFetchingFollowButton: []
}

export const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_ALL_USERS_ON_ACTIVE_PAGE:
            return setStateValueInObject(state, 'allUsersOnActivePage', action.items)

        case SET_ALL_USERS_IN_SERVICE:
            return setStateValueInObject(state, 'allUsersInService', action.totalCount)

        case SET_IS_FETCHING_USERS:
            return setStateValueInObject(state, 'isFetchingUsers', action.mode)

        case SET_SUBSCRIBE_TO_USER:
            return setStateValueInObject(
                state,
                'allUsersOnActivePage',
                addValueInArray(
                    state.allUsersOnActivePage,
                    loopThroughArrayWithCallback(
                        state.allUsersOnActivePage,
                        (user) => {
                            return user.id === action.id
                                ? user.followed = action.mode
                                : user
                        }
                    )
                )
            )

        case SET_IS_FETCHING_FOLLOW_BUTTON:
            return setStateValueInObject(
                state,
                'isFetchingFollowButton',
                booleanCheckCondition(
                    action.mode,
                    addValueInArray(state.isFetchingFollowButton, action.id),
                    state.isFetchingFollowButton.filter(id => id !== action.id)
                )
            )

        default: return {
            ...state
        }
    }
};

const setAllUsersOnActivePage = (items) => ({
    type: SET_ALL_USERS_ON_ACTIVE_PAGE,
    items
}),

    setAllUsersInService = (totalCount) => ({
        type: SET_ALL_USERS_IN_SERVICE,
        totalCount
    }),

    setIsFetchingUsers = (mode) => ({
        type: SET_IS_FETCHING_USERS,
        mode
    }),

    setSubscribeToUser = (id, mode) => ({
        type: SET_SUBSCRIBE_TO_USER,
        id,
        mode
    }),

    setIsFetchingFollowButton = (id, mode) => ({
        type: SET_IS_FETCHING_FOLLOW_BUTTON,
        id,
        mode
    })


export const setAllUsersOnActivePageT = (count, page) => async (dispatch) => {
    dispatch(setIsFetchingUsers(true))
    const { data: { items, totalCount } } = await usersApi.getAllUsersOnActivePage(count, page)
    dispatch(setAllUsersOnActivePage(items))
    dispatch(setAllUsersInService(totalCount))
    dispatch(setIsFetchingUsers(false))
}

export const setSubscribeToUserT = (id, mode) => async (dispatch) => {
    dispatch(setIsFetchingFollowButton(id, true))
    const apiMethod = mode ? usersApi.setUserFollow : usersApi.setUserUnFollow
    const { resultCode } = await apiMethod(id)
    if (resultCode === 0) {
        dispatch(setSubscribeToUser(id, mode))
        dispatch(setIsFetchingFollowButton(id, false))
    }
}