import { Box, Button, IconButton } from '@material-ui/core';
import { AccountCircle, ExitToApp } from '@material-ui/icons';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { booleanCheckCondition } from '../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { useAppHeaderStyles } from '../styles';


export const UserInfo = ({
    authData: {
        isAuth,
        id
    },
    logOffServiceT
}) => {

    const {
        header__nav,
        nav__content_$wrapper,
        nav__profile_$icon,
        nav__logoff_$icon,
        nav__login
    } = useAppHeaderStyles();

    return (
        <Box className={header__nav}>
            <ul className={nav__content_$wrapper}>
                {booleanCheckCondition(
                    isAuth,
                    <>
                        <li>
                            <NavLink to={`/profile/${id}`}>
                                <IconButton aria-label='Profile'>
                                    <AccountCircle className={nav__profile_$icon} />
                                </IconButton>
                            </NavLink>
                        </li>
                        <li>
                            <IconButton aria-label='Log off' onClick={logOffServiceT}>
                                <ExitToApp className={nav__logoff_$icon} />
                            </IconButton>
                        </li>
                    </>,
                    <li>
                        <NavLink to={'/login'}>
                            <Button aria-label='Log in' className={nav__login}>
                                Login
                                </Button>
                        </NavLink>
                    </li>
                )}
            </ul>
        </Box>
    )
};