import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { getIsAuth } from '../../../../redux/selectors/auth-selectors';
import { booleanCheckCondition } from '../../../../utils/utilsForHandlingCondition/booleanCheckCondition';
import { withRedirect } from './withRedirect';

const mapStateToProps = (state) => ({
    isAuth: getIsAuth(state)
})

export const withAuthRedirect = (Component, link, type) => ({
    default: connect(mapStateToProps)((props) => {
        return withRedirect(props.isAuth, Component, props, link)
    }),
    invert: connect(mapStateToProps)((props) => {
        return booleanCheckCondition(
            props.isAuth,
            <Component {...props} />,
            <Redirect to={`/${link}`} />
        )
    })
})[type];