import { Box } from '@material-ui/core';
import React from 'react';
import { AccountCircle, Email, Group, LineWeight, MusicNote, Settings } from '@material-ui/icons';
import { NavbarItem } from './navbarItem/NavbarItem';
import { useNavbarStyles } from '../../styles';

const NavbarBlock = ({
    id
}) => {

    const {
        content_$wrapper,
        link_$text,
    } = useNavbarStyles()

    return (
        <nav>
            <ul className={content_$wrapper}>
                <NavbarItem ariaLabelBtn='Profile' urlLink={`/profile/${id}`}>
                    <AccountCircle />
                    <Box className={link_$text}>
                        Паспортъ
                    </Box>
                </NavbarItem>

                <NavbarItem ariaLabelBtn='News' urlLink={'/feed'}>
                    <LineWeight />
                    <Box className={link_$text}>
                        Событiя
                    </Box>
                </NavbarItem>

                <NavbarItem ariaLabelBtn='Messages' urlLink={'/dialogs'}>
                    <Email />
                    <Box className={link_$text}>
                        Телеграмма
                    </Box>
                </NavbarItem>

                <NavbarItem ariaLabelBtn='Users' urlLink={'/users'}>
                    <Group />
                    <Box className={link_$text}>
                        Община
                    </Box>
                </NavbarItem>

                <NavbarItem ariaLabelBtn='Music' urlLink={'/music'}>
                    <MusicNote />
                    <Box className={link_$text}>
                        Грамофонъ
                    </Box>
                </NavbarItem>

                <NavbarItem ariaLabelBtn='Settings' urlLink={'/settings'}>
                    <Settings />
                    <Box className={link_$text}>
                        Настройкi
                    </Box>
                </NavbarItem>
            </ul>
        </nav>
    );
};

export default NavbarBlock;