import { Checkbox, FormControlLabel } from '@material-ui/core';
import React from 'react';

export const InputCheckbox = ({ input, meta, label, className, ...optional }) => {
    return (
        <FormControlLabel
            control={<Checkbox {...input} {...optional} color='primary' />}
            label={label}
            aria-label={label}
            className={className}
            required={true}
        />
    )
};