export const profilerOnRender = (
    id,
    phase,
    actualDuration,
    baseDuration,
    startTime,
    commitTime,
    interactions
) => {
    console.table(`%c${phase.toUpperCase()}: ${id} ${startTime} - ${commitTime}`,
        `background-color: #111;
        color: #B6E7E2;
        padding: 5px 10px;
        border-radius: 2px;
    `);

    console.dir({
        actualDuration: `${actualDuration.toFixed(2)}ms`,
        baseDuration: `${baseDuration.toFixed(2)}ms`,
        interactions
    });

    console.table('%c End',
        `color: #6ABBCD;
        border-radius: 2px;
    `)
};