import { makeStyles } from '@material-ui/core';

export const useLoginStyles = makeStyles(() => ({
    form__login: {
        display: 'grid',
        placeItems: 'center',
        color: '#333333',
        maxWidth: '500px',
        margin: '0 auto'
    },

    form__login__$wrapper: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: '6px',
        padding: '20px',
        backgroundColor: 'rgb(255 255 255 / .5)'
    },

    form__login__field: {
        margin: '0',
        marginBottom: '20px',
        width: '100%',
        color: '#333333'
    },

    form__login__btn_$wrapper: {
        display: 'grid',
        placeItems: 'center',
        width: '100%',
        height: '40px'
    },

    form__login__btn_$submit: {
        width: '100%',
        height: '100%'
    },

    captcha_$wrapper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'flex-start',
        marginBottom: '20px'
    },

    captcha_$img: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'flex-start',
        justifyContent: 'center',
        width: '100%',
        marginBottom: '10px'
    }
}))