import { Paper, Typography } from '@material-ui/core';
import React from 'react';
import { memo } from 'react';
import { useLocationPageStyles } from './styles'

export const LocationPage = memo(({ location }) => {

    const {
        locationPage
    } = useLocationPageStyles()

    return <Paper className={locationPage}>
        <Typography variant='h6'>
            {location}
        </Typography>
    </Paper>
});