import { TextField } from '@material-ui/core';
import React from 'react';
import { booleanCheckCondition } from '../../../../utils/utilsForHandlingCondition/booleanCheckCondition';

export const InputStandard = ({ input, meta: { touched, error, dirty }, ...optional }) => {

    const isError = booleanCheckCondition(touched && error && dirty)

    return <TextField error={isError} {...input} {...optional} />
}